<template>
  <div id="triangle-box" class="triangle-box" :class="{'triangle-box-hover': isHover}">
    <!--  start -->
    <div class="triangle-box-border">
      <div class="triangle-box-border-left"></div>
      <div class="triangle-box-border-right"></div>
    </div>
    <!--  end -->

    <!--  start -->
    <div class="triangle-box-triangle-top-left"></div>
    <div class="triangle-box-triangle-bottom-right"></div>
    <!--  end -->

    <!--  start -->
    <div class="triangle-content">
      <slot></slot>
    </div>
    <!--  end -->
  </div>
</template>

<script>
export default {
  name: "ZTriangleBox",
  props: {
    isHover: {
      type: Boolean,
      default: true
    }
  }
};
</script>

