<template>
  <div id="modal" class="default-modal" v-if="visible">
    <!--  start -->
    <div class="modal-mask" v-show="visibleMask" @click="maskClose"></div>
    <!--  end -->

    <!--  start -->
    <div class="modal-wrap">
      <div class="modal-content" :style="{ width: width, height: height }">
        <div class="modal-bg-img">
          <!--  -->
          <slot name="modalBg"></slot>
        </div>

        <div class="modal-head">
          <!--  stsrat -->
          <div class="head-title">
            <img
              class="head-title-bg"
              src="../../assets/img/components/img-moadl-title-bg.png"
            />

            <div class="title-text">{{ title }}</div>
            <img
              class="head-title-bg head-title-bg-right"
              src="../../assets/img/components/img-moadl-title-bg.png"
            />
          </div>
          <!--  end -->

          <!--  start -->
          <div class="modal-close" @click="$emit('close', !visible)"></div>
          <!--  end -->
        </div>
        <div class="modal-body">
          <!--  -->
          <slot></slot>
        </div>
      </div>
    </div>
    <!--  end -->
  </div>
</template>

<script>
export default {
  name: "modal",
  model: {
    prop: "visible",
    event: "close"
  },
  props: {
    // 
    width: {
      type: String || Number,
      default: "46.094vw"
    },
    // 
    height: {
      type: String || Number,
      default: "26.25vw"
    },
    // 
    title: {
      type: String,
      default: ""
    },
    // 
    visible: {
      type: Boolean,
      default: false
    },
    // 
    maskClosable: {
      type: Boolean,
      default: false
    },
    // 
    visibleMask: {
      type: Boolean,
      default: true
    }
  },

  methods: {
    /**
     * 
     */
    maskClose() {
      /**
       * 
       */
      if (this.maskClosable) {
        this.$emit("close", !this.visible);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "./index";
</style>
