<template>
  <div class="pagination">
    <!--  start -->
    <div class="page-sizes">
      <span></span>
      <a-select v-model="pageSize" @change="handleSelectChange">
        <a-icon slot="suffixIcon" type="caret-down" theme="filled" />
        <a-select-option
          v-for="item in pageSizeOptions"
          :key="item"
          :value="item"
        >
          {{ item }}
        </a-select-option>
      </a-select>
      <span></span>
    </div>
    <!--  end -->

    <!--  start -->
    <a-pagination
      v-model="page"
      :total="total || 1"
      :page-size="pageSize"
      :item-render="itemRender"
      @change="handlePaginationChange"
    />
    <!--  end -->
  </div>
</template>

<script>
export default {
  name: "ZPagination",
  props: {
    // 
    total: {
      type: Number,
      default: 500
    }
  },
  data() {
    return {
      pageSizeOptions: [10, 20, 30, 40, 50], // 
      page: 1, // 
      pageSize: 10 // 
    };
  },
  methods: {
    /**
     * 
     * @page 
     * @pageSize 
     */
    setData(page, pageSize) {
      if (page) {
        this.page = page;
      }
      // 
      let pageSizeOption = this.pageSizeOptions.find(item => {
        return item == pageSize;
      });
      // 
      if (pageSizeOption) {
        this.pageSize = pageSize;
      }
    },

    /**
     * 
     */
    itemRender(current, type, originalElement) {
      if (type === "prev") {
        return "";
      } else if (type === "next") {
        return "";
      } else if (type === "page") {
        return current;
      }
      return originalElement;
    },

    /**
     * 
     * @page 
     * @pageSize 
     */
    handlePaginationChange(page, pageSize) {
      /**
       * 
       */
      this.$emit("change", page, pageSize);
    },

    /**
     * pageSize 
     * @pageSize 
     */
    handleSelectChange(pageSize) {
      let page = this.page;
      let totalPage = parseInt(this.total / pageSize);
      if (this.total % pageSize != 0) {
        totalPage += 1;
      }
      if (totalPage < page) {
        page = totalPage;
      }
      this.page = page;
      /**
       * 
       */
      this.$emit("change", page, pageSize);
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
